import React from 'react'

const Logo = ({images, setShow}) => {

      const hideHome = () => {
            setShow(true)
      }

      return (
            <div className="logo_context flex">
                  <div className="logo_content__circle flex">
                        <div className="circle_effect"></div>
                        <div className="logo_content__img flex">
                              <img src={images.logo} alt="Techno Player" />
                        </div>
                  </div>
                  <button className="btn_default open" onClick={() => hideHome()}>
                        <span>Entrar</span>
                  </button>
            </div>
      )
}

export default Logo
