import React from 'react'
import { IoLogoWhatsapp } from 'react-icons/io'
import useAppContext from '../../hook/useAppContext'
import Online from '../online/Online'

const User = () => {

      const {info} = useAppContext()
      const {user, links} = info 

      return (
            <div className="user_profile">
                  <div className="container">
                        <div className="user_profile__content flex">
                              <div className="user_avatar">
                                    <img src={user.avatar} alt={user.name} />
                              </div>
                              <div className="user_data">
                                    <p>Bienvenido a mi xatspace!</p>
                                    <h2>{user.name}</h2>
                                    <span>{user.status}</span>
                                    <Online id={user.id} />
                              </div>
                        </div>
                        <div className="navbar flex">
                              <a className="btn_grad yellow" href={links.chat} target="_blank" rel="noreferrer" ><span>Chat</span></a>
                              <a className="btn_grad purple" href={links.gallery} target="_blank" rel="noreferrer" ><span>Reproductores</span></a>
                              <button className="btn_grad blue"><IoLogoWhatsapp /><span>Cazadora - Juanmix</span></button>
                        </div>
                  </div>
            </div>
      )
}

export default User