import React, { useEffect, useState } from 'react'
import './online.css'

const Online = ({id}) => {

      const [online, setOnline] = useState([])

      async function getOnline(id_user){

            try{
                  const response = await fetch(`https://oceanbot.net/online.php?id=${id_user}`)
                  const result = await response.json()
                  setOnline(result)
            }
            catch(err){
                  console.log(err)
            }
      }

      useEffect(() => {
            getOnline(id)
      }, [id])

      return (
            <div className="online_content flex">
                  <div className={online.status === "online" ? "dot green" : "dot red"}></div>
                  <span>{online.status === "online" ? "Online Now" : "Offline"}</span>
            </div>
      )
}

export default Online
