
import Slider from "react-slick"
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"

const SlideContent = ({plans}) => {

      const settings = {
              dots: true,
              arrows: false,
              infinite: true,
              speed: 600,
              slidesToShow: 1,
              slidesToScroll: 1,
              variableWidth: true,
              autoplay: true,
              autoplaySpeed: 2000,
              cssEase: 'ease-in-out'
      }

      return (
            <div>
                  <Slider {...settings}>
                        {
                              plans.map((item, index) => (
                                    <div className="card_plan" key={index}>
                                          <div className={`card_header ${item.class} flex`}>
                                                <h2>{item.price}</h2>
                                                <span>Xats</span>
                                          </div>
                                          <div className="card_body flex">
                                                <div className="card_body__title">
                                                      <h2>{item.name}</h2>
                                                </div>
                                                <div className="card_body__list">
                                                      <ul>
                                                            <li>{item.upload}</li>
                                                            <li>{item.bitrate}</li>
                                                      </ul>
                                                </div>
                                          </div>
                                    </div>
                              ))
                        }
                        
                  </Slider>
            </div>
      )
                
}

export default SlideContent
