import React from 'react'
import useAppContext from '../../hook/useAppContext'
import Home from '../home/Home'
import Profile from '../profile/Profile'
import './main.css'

const Main = () => {

      const {info} = useAppContext()

      if(info.length === 0){
            return
      }

      return (
            <div className="main_content">
                  <Home />
                  <Profile />
            </div>
      )
}

export default Main
