import React from 'react'

const Banner = ({images}) => {

      return (
            <div className="banner">
                  <div className="banner_content flex">
                        <img src={images.bg} alt="banner server" />
                  </div>
                  <div className="banner_content__text flex">
                        <div className="banner_title flex">
                              <h1 className="t-grad">Techno Player</h1>
                              <div className="cornner"></div>
                              <div className="cornner"></div>
                              <div className="cornner"></div>
                              <div className="cornner"></div>
                        </div>
                        <div className="banner_description">
                              <p>Servidores de radio de alta calidad, servicio, precio y atención personal</p>
                        </div>
                        <div className="banner_logo__panel">
                              <img src={images.panel} alt="Sonicpanel" />
                        </div>
                  </div>
            </div>
      )
}

export default Banner
