import React from 'react'
import SlideContent from './SlideContent'
import useAppContext from '../../hook/useAppContext'

const Content = () => {

      const {info} = useAppContext() 
      
      return (
            <div className="content_page">
                  <div className="container page flex">
                        <div className="title_section flex">
                              <h1 className="t-grad">Paquetes Panel</h1>
                        </div>
                        <div className="slide_content">
                              <SlideContent plans={info.plans}/>
                        </div>
                        <div className="description_page">
                              <p>Servidores de radio de alta calidad, servicio, precio y atención personal</p>
                        </div>
                  </div>
            </div>
      )
}

export default Content
